___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }
 
  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }
 
  .app-bar .app-bar__menu {
    display: none;
  }
 
  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }
 
  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }
 
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }
}
 
@media screen and (min-width: 800px) {
  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
}

/*Beranda*/
/* Kode CSS responsif */

/* Perangkat dengan lebar layar maksimum 992px */
@media (max-width: 992px) {
  .about {
    flex-direction: column;
    align-items: center;
  }
  .aboutDescription {
    text-align: center;
  }
  .aboutDescription button {
    width: 100%;
  }
}

/* Perangkat dengan lebar layar maksimum 768px */
@media (max-width: 768px) {
  article {
    padding: 0 50px;
  }
  .aboutImg img {
    width: 100%;
    height: auto;
  }
  .aboutDescription {
    font-size: 12px;
  }
  .cards-project .card,
  .article-list .card-article {
    width: 150px;
    height: 230px;
  }
  .cards-project img,
  .article-list img {
    width: 100%;
    height: auto;
  }
}

/* Perangkat dengan lebar layar maksimum 576px */
@media (max-width: 576px) {
  article {
    padding: 0 20px;
  }
  .aboutWe {
    padding: 50px;
  }
  .join button {
    width: 100%;
    height: 40px;
  }
  .join button a {
    font-size: 18px;
  }
  .article h2,
  .galeri h2 {
    font-size: 18px;
  }
  .bg-join {
    height: auto;
    padding: 20px;
  }
  hr {
    width: 100%;
  }
  .cards-project .card,
  .article-list .card-article {
    width: 100px;
    height: 180px;
  }
  .cards-project img,
  .article-list img {
    width: 100%;
    height: auto;
  }
}

/* Detail */
/* Kode CSS responsif untuk halaman detail */


/* Perangkat dengan lebar layar maksimum 992px */
@media (max-width: 992px) {
  .detail {
    flex-direction: column;
    align-items: center;
  }
  .detailAboutDescription {
    text-align: center;
  }
}

/* Perangkat dengan lebar layar maksimum 768px */
@media (max-width: 768px) {
  .detailImg img {
    width: 100%;
    height: auto;
  }
  .detailAboutDescription {
    font-size: 12px;
    padding: 0 20px;
  }
}

/* Perangkat dengan lebar layar maksimum 576px */
@media (max-width: 576px) {
  .detailAboutDescription {
    font-size: 10px;
    padding: 0 10px;
  }
  .lokasi p {
    font-size: 12px;
  }
}

/* Daftar Artikel */
/* Kode CSS responsif untuk halaman daftar artikel */


/* Perangkat dengan lebar layar maksimum 992px */
@media (max-width: 992px) {
  .article-body {
    flex-direction: column;
    align-items: center;
  }
  .article-body img {
    width: 100%;
    height: auto;
  }
  .textArtikel h3 {
    font-size: 20px;
    text-align: center;
  }
  .textArtikel p {
    text-align: center;
  }
}

/* Perangkat dengan lebar layar maksimum 768px */
@media (max-width: 768px) {
  .artikelList {
    padding: 0 20px;
  }
  .textArtikel h3 {
    font-size: 18px;
  }
  .textArtikel p {
    font-size: 12px;
  }
}

/* Perangkat dengan lebar layar maksimum 576px */
@media (max-width: 576px) {
  .artikelList {
    padding: 0 10px;
  }
  .textArtikel h3 {
    font-size: 16px;
  }
  .textArtikel p {
    font-size: 10px;
  }
  .lokasi p {
    font-size: 12px;
  }
}
.cards-project {
  display: flex;
  gap: 2rem;
}

.card {
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.card img {
  width: 100%;
  height: 150px;
}

.card-body {
  padding: 10px;
}

@media screen and (max-width: 768px) {
  .cards-project {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media screen and (max-width: 480px) {
  .cards-project {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}
